@import "import/base";
@import "import/typography";
@import "import/reset-css";

@import "import/lib-fonts";

// clearfix
.clearfix{
	*zoom:1;
	&:before, &:after{
		content:"";
		display:block;
	}
	&:after{
		clear:both;
	}
}

body{
	font-weight: 400;
	color: #222;
	line-height:1;
	font-family: "SCDream", "Noto Sans KR",sans-serif;
	word-break:keep-all;
}

.wrap{
	position:relative;
	width:100%;
	min-width:rem(320px);
	letter-spacing:-0.035em;
	overflow:hidden;
}

.w1440{
	width:100%;
	max-width:1440px;
	margin:0 auto;
	position:relative;
}

.raleway{
	font-family:"Raleway", "SCDream", "Noto Sans KR",sans-serif;
	letter-spacing:normal;
}

.black_bg{width:100%; height:100%; position:fixed; top:0; left:0; background:rgba(0, 0, 0, 0.6); display:none;}

.p_only{display:block !important;}
.p_t_only{display:block !important;}
.t_only{display:none !important;}
.t_m_only{display:none !important;}
.m_only{display:none !important;}

.pc_img{display:inline !important;}
.pc_ta_img{display:inline !important;}
.ta_img{display:none !important;}
.ta_mo_img{display:none !important;}
.mo_img{display:none !important;}

.btn_wrap{margin-top:40px; text-align:center; font-size:0;}
.btn_wrap a{display:inline-block; margin-right:14px;}
.btn_wrap a:last-of-type{margin-right:0;}
.roundBtn{
	display:inline-block; width:180px; height:48px; line-height:48px; font-size:16px; font-weight:600; border-radius:6px; text-align:center;
	&.big{width:300px; height:60px; line-height:60px; font-size:18px;}
	&.orange{background:#ff8400; color:#fff;}
	&.purple{background:#272a44; color:#fff;}
}

.lineRoundBtn{
	display:inline-block; width:180px; height:48px; line-height:46px; font-size:16px; font-weight:600; border-radius:6px; text-align:center;
	&.big{width:300px; height:60px; line-height:58px; font-size:18px;}
	&.orange{border:1px solid #ff8400; color:#ff8400;}
	&.purple{border:1px solid #272a44; color:#272a44;}
	&.gray{border:1px solid #ddd; color:#999;}
}

.underlineBtn{
	display:inline-block; font-size:16px; padding-bottom:1px; vertical-align:middle;
	&.orange{border-bottom:1px solid #ff8400; color:#ff8400;}
	&.purple{border-bottom:1px solid #272a44; color:#272a44;}
}

.checkbox {display:inline-block; position:relative;}
.checkbox input[type="checkbox"] {width:0; height:0; position:absolute; top:0; left:0; z-index:0;}
.checkbox input[type="checkbox"] + label {display:inline-block; line-height:1.3; padding-left:30px; font-size:16px; color:#666; background:url("/assets/img/common/check_off_white.png") top left no-repeat; background-size:auto 20px; position:relative; z-index:1;}
.checkbox input[type="checkbox"]:checked + label {background-image:url("/assets/img/common/check_on.png");}

.smallTab{padding-bottom:30px;}
.smallTab.bb2{border-bottom: 2px solid #1b1b1b;}
.smallTab ul{font-size:0;}
.smallTab ul li{display:inline-block; padding-right:60px; vertical-align:top;}
.smallTab ul li:last-of-type{padding-right:0;}
.smallTab ul li a{display:block; font-size:22px;}
.smallTab ul li.on a{color:#ff8400; font-weight:600;}
.centerScrollTab{padding:80px 60px; text-align:center;}
.centerScrollTab ul{white-space:nowrap; position:relative; z-index:10;}
.centerScrollTab ul li{padding-right:0;}
.centerScrollTab ul li a{padding:24px; font-size:30px; position:relative;}
.centerScrollTab ul li.on a:after{content:""; display:block; width:100%; height:4px; background:#ff8400; position:absolute; bottom:0; left:0;}
.centerScrollTab .mCustomScrollBox{z-index:1;}
.centerScrollTab .mCS-minimal-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar{background-color: #ddd;}
.centerScrollTab .mCustomScrollBox.mCS-minimal-dark + .mCSB_scrollTools.mCSB_scrollTools_horizontal{height:4px; margin:0; z-index:0;}


@media all and (max-width:1520px){
	.w1440{max-width:100%; padding:0 60px;}
}

@media all and (max-width:1080px){ /* tablet */
	.p_only{display:none !important;}
	.p_t_only{display:block !important;}
	.t_only{display:block !important;}
	.t_m_only{display:block !important;}
	.m_only{display:none !important;}

	.pc_img{display:none !important;}
	.pc_ta_img{display:inline !important;}
	.ta_img{display:inline !important;}
	.ta_mo_img{display:inline !important;}
	.mo_img{display:none !important;}

	.w1440{width:100%; padding:0 40px; margin:0;}

	.btn_wrap a{margin-right:20px;}
	.roundBtn{
		width:calc((100% - 20px) / 2); height:70px; line-height:70px; font-size:26px; font-weight:500;
		&.big{width:100%; height:90px; line-height:90px; font-size:28px;}
	}
	.lineRoundBtn{
		width:calc((100% - 20px) / 2); height:70px; line-height:66px; font-size:26px; font-weight:600;
		&.big{width:100%; height:90px; line-height:86px; font-size:28px;}
		&.orange{border-width:2px;}
		&.purple{border-width:2px;}
	}

	.underlineBtn{
		font-size:22px; padding-bottom:4px;
		&.orange{border-bottom-width:2px;}
		&.purple{border-bottom-width:2px;}
	}

	.checkbox input[type="checkbox"] + label {padding-left:45px; font-size:24px; background-size:auto 30px;}

	.centerScrollTab{margin-bottom:9.26vw; padding:0; text-align:left; position:relative;}
	.centerScrollTab ul{padding:0 3.70vw;}
	.centerScrollTab ul li a{padding:2.78vw; font-size:3.33vw;}
	.centerScrollTab ul li.on a:after{height:0.37vw; z-index:1;}
	.centerScrollTab .mCustomScrollBox.mCS-minimal-dark + .mCSB_scrollTools.mCSB_scrollTools_horizontal{height:0.37vw;}
}

@media all and (max-width:770px){ /* mobile */
	.p_only{display:none !important;}
	.p_t_only{display:none !important;}
	.t_only{display:none !important;}
	.t_m_only{display:block !important;}
	.m_only{display:block !important;}

	.pc_img{display:none !important;}
	.pc_ta_img{display:none !important;}
	.ta_img{display:none !important;}
	.ta_mo_img{display:inline !important;}
	.mo_img{display:inline !important;}

	.w1440{padding:0 5.56vw;}

	.btn_wrap{margin-top:5.56vw;}
	.btn_wrap a{margin-right:2.78vw;}
	.roundBtn{
		width:calc((100% - 2.78vw) / 2); height:9.72vw; line-height:9.72vw; font-size:3.61vw; border-radius:0.83vw;
		&.big{height:12.50vw; line-height:12.50vw; font-size:3.89vw;}
	}
	.lineRoundBtn{
		width:calc((100% - 2.78vw) / 2); height:9.72vw; line-height:9.17vw; font-size:3.61vw; border-radius:0.83vw; 
		&.big{height:12.50vw; line-height:11.94vw; font-size:3.89vw;}
		&.orange{border-width:0.28vw;}
		&.purple{border-width:0.28vw;}
	}
	.underlineBtn{
		font-size:3.06vw; padding-bottom:0.56vw;
		&.orange{border-bottom-width:0.28vw;}
		&.purple{border-bottom-width:0.28vw;}
	}

	.checkbox input[type="checkbox"] + label {padding-left:6.25vw; font-size:3.33vw; background-size:auto 4.17vw;}

	.smallTab{padding-bottom:13.89vw; position:relative;}
	.smallTab.bb2{border-bottom:0;}
	.smallTab .mSelect{width:100%; border:0.28vw solid #ddd; border-radius:1.39vw; position:absolute; top:0; left:0; background:#fff;}
	.smallTab p{height:10.83vw; line-height:10.83vw; font-size:3.89vw; padding:0 11.81vw 0 5.56vw; background:url("/assets/img/layout/moMenu_arrow_d.png") center right 5.56vw no-repeat; background-size:4.31vw auto;}
	.smallTab p.on{background:url("/assets/img/layout/moMenu_arrow_u.png") center right 5.56vw no-repeat; background-size:4.31vw auto;}
	.smallTab ul{display:none;}
	.smallTab ul li{display:block; padding:0; margin-bottom:1.39vw;}
	.smallTab ul li:last-of-type{margin-bottom:2.78vw;}
	.smallTab ul li a{display:block; padding:1.39vw 5.56vw; font-size:3.89vw;}
	.smallTab ul li.on a{color:inherit; font-weight:400;}
	.centerScrollTab{padding-bottom:0; margin-bottom:13.89vw;}
	.centerScrollTab:after{height:0.28vw;}
	.centerScrollTab ul{display:block; padding:0 5.56vw;}
	.centerScrollTab ul li{display:inline-block; margin-bottom:0;}
	.centerScrollTab ul li:last-of-type{margin-bottom:0;}
	.centerScrollTab ul li a{padding:4.17vw; font-size:5vw;}
	.centerScrollTab ul li.on a{color:#ff8400; font-weight:600;}
	.centerScrollTab ul li.on a:after{height:0.56vw;}
	.centerScrollTab .mCustomScrollBox.mCS-minimal-dark + .mCSB_scrollTools.mCSB_scrollTools_horizontal{height:0.56vw;}
}
